import React, { Component, Fragment } from "react";

import CssBaseline from "@material-ui/core/CssBaseline";

import HomePage from "./components/Default/HomePage";
import { Provider } from "react-redux";
import { loadUser } from "./actions/auth";
import store from "./store";

import { Provider as AlertProvider } from "react-alert";
import AlertTemplate from "react-alert-template-basic";
import Alerts from "./layout/Alerts";

import NavbarNew from "./components/Default/Navbar_new";
// import Footer from "./components/Default/Footer";
import Footer from "./src/components/Footer/Footer";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import PrivateRoute from "./common/PrivateRoute";

import StepForm from "./components/TransactionForm_new/StepForm";
// import LogIn from "../Sign-in/Login";
import Login from "./components/Sign-in/Login_new";
import Register from "./components/Sign-in/Register_new";
// import SignUp from "../Sign-in/SignUp";
import SellerForm from "./components/TransactionForm_new/SellerForm";
import ResetPassword from "./components/Sign-in/ResetPassword";
import ResetPasswordConfirm from "./components/Sign-in/ResetPasswordConfirm";
import FAQ from "./components/Default/FAQ";
import Disputes from "./components/Default/Disputes";
import AboutUs from "./components/Default/AboutUs";
import SellerFirstPage from "./components/Default/SellerFirstPage";
import Header from "./kit_components/components/Header/Header";
import HeaderLinks from "./kit_components/components/Header/HeaderLinks";
// import HeaderLinks from "./components/Default/HeaderLinks";
// import Dashboard from "./components/Transaction Form/Dashboard";
import secufer_logo from "./components/Default/images/secufer_logo_name.png";

const alertOptions = {
  timeout: 3000,
  position: "top center",
};

// function App() {
class App extends Component {
  componentDidMount() {
    store.dispatch(loadUser());
  }
  render() {
    const dashboardRoutes = [];
    // const user_obj = JSON.parse(localStorage.getItem("user"));

    return (
      <Provider store={store}>
        <AlertProvider template={AlertTemplate} {...alertOptions}>
          <Router basename={process.env.PUBLIC_URL}>
            <Fragment>
              <NavbarNew />
              {/* <Header
                color="red"
                routes={dashboardRoutes}
                brand={secufer_logo}
                rightLinks={<HeaderLinks />}
                fixed
                changeColorOnScroll={{
                  height: 400,
                  color: "white",
                }}
              /> */}

              <Alerts />

              <div className="App">
                <CssBaseline />
                <Switch>
                  <Route exact path="/" component={HomePage} />
                  <Route exact path="/about us" component={AboutUs} />
                  <Route exact path="/faq" component={FAQ} />
                  <Route exact path="/disputes" component={Disputes} />
                  <Route
                    exact
                    path="/seller_home"
                    component={SellerFirstPage}
                  />

                  {/* <Route path="/api/transactions/" component={SellerForm} /> */}
                  <Route exact path="/login" component={Login} />
                  <Route exact path="/register" component={Register} />
                  <Route
                    exact
                    path="/reset_password"
                    component={ResetPassword}
                  />
                  <Route
                    exact
                    path="/password/reset/confirm/:uid/:token"
                    component={ResetPasswordConfirm}
                  />

                  <PrivateRoute exact path="/form" component={StepForm} />
                  <PrivateRoute
                    exact
                    path="/api/transactions/"
                    // component={SellerForm}
                  >
                    <SellerForm />
                  </PrivateRoute>
                  {/* <PrivateRoute exact path="/form" component={Dashboard} /> */}
                  {/* <StepForm /> */}
                  {/* </Route> */}
                  {/* <Route path="/login" component={LogIn} /> */}
                  {/* <Route path="/signup" component={SignUp} /> */}
                  {/* <SellerForm /> */}

                  {/* <p>URL Seller</p> */}
                  {/* </Route> */}
                </Switch>
                {/* <Navbar /> */}

                {/* <HomePage /> */}
              </div>
              <Footer />
            </Fragment>
          </Router>
        </AlertProvider>
      </Provider>
    );
  }
}

export default App;

