import axios from "axios";
import { createMessage, returnErrors } from "./messages";
import { tokenConfig } from "./auth";

import {
  ADD_TRANSACTION,
  GET_TRANSACTIONS,
  GET_SINGLE_TRANSACTION,
  DELETE_TRANSACTION,
} from "./types";

// GET LEADS
export const getTransactions = (form_pk) => (dispatch, getState) => {
  axios
    .get(
      `http://127.0.0.1:8000/api/transactions/?form_pk=${form_pk}`,
      tokenConfig(getState)
    )
    .then((res) => {
      dispatch({
        type: GET_TRANSACTIONS,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

export const getSingleTransaction = (form_pk) => (dispatch, getState) => {
  axios
    .get(
      `http://127.0.0.1:8000/api/transactions/?form_pk=${form_pk}/`,
      tokenConfig(getState)
    )
    .then((res) => {
      dispatch({
        type: GET_SINGLE_TRANSACTION,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};

// DELETE LEAD
export const deleteTransaction = (id) => (dispatch, getState) => {
  axios
    .delete(
      `http://127.0.0.1:8000//api/transactions/${id}/`,
      tokenConfig(getState)
    )
    .then((res) => {
      dispatch(createMessage({ deleteTransaction: "Transaction Deleted" }));
      dispatch({
        type: DELETE_TRANSACTION,
        payload: id,
      });
    })
    .catch((err) => console.log(err));
};

// ADD LEAD
export const addTransaction = (transaction) => (dispatch, getState) => {
  axios
    .post(
      "http://127.0.0.1:8000/api/transactions/",
      transaction,
      tokenConfig(getState)
    )
    .then((res) => {
      dispatch(createMessage({ addTransaction: "Transaction Added" }));
      dispatch({
        type: ADD_TRANSACTION,
        payload: res.data,
      });
    })
    .catch((err) =>
      dispatch(returnErrors(err.response.data, err.response.status))
    );
};
