import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { register } from "../../actions/auth";
import { createMessage } from "../../actions/messages";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";

import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import ListItem from "@material-ui/core/ListItem";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

const styles = (theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
});

export class Register extends Component {
  // function Register() {
  state = {
    phone_number: "",
    email: "",
    first_name: "",
    last_name: "",
    user_type: "",
    password: "",
    password2: "",
    checkedC: "",
  };

  static propTypes = {
    register: PropTypes.func.isRequired,
    isAuthenticated: PropTypes.bool,
    isBuyer: PropTypes.string,
  };

  onSubmit = (e) => {
    e.preventDefault();
    const {
      phone_number,
      email,
      first_name,
      last_name,
      user_type,
      password,
      password2,
    } = this.state;
    if (password !== password2) {
      this.props.createMessage({ passwordNotMatch: "Passwords do not match" });
    } else {
      const newUser = {
        phone_number,
        email,
        first_name,
        last_name,
        user_type,
        password,
      };
      this.props.register(newUser);
    }
  };

  onChange = (e) => this.setState({ [e.target.name]: e.target.value });
  handleChange = (event) => {
    this.setState({ [event.target.name]: event.target.checked });
  };

  render() {
    const { classes } = this.props;

    // const { checkedC } = state;

    if (
      this.props.isAuthenticated &&
      this.props.isBuyer.user_type === "Buyer"
    ) {
      return <Redirect to="/form" />;
    } else if (
      this.props.isAuthenticated &&
      this.props.isBuyer.user_type === "Seller"
    ) {
      return <Redirect to="/faq" />;
    }
    const {
      phone_number,
      email,
      first_name,
      last_name,
      user_type,
      password,
      password2,
      checkedC,
    } = this.state;
    const isValid = checkedC;
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <form className={classes.form} onSubmit={this.onSubmit} noValidate>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12}>
                <FormControl fullWidth margin="normal">
                  <InputLabel>I am</InputLabel>
                  <Select
                    value={user_type}
                    onChange={this.onChange}
                    name="user_type"
                  >
                    <MenuItem value={"Buyer"}>Buyer</MenuItem>
                    <MenuItem value={"Seller"}>Seller</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={6} sm={6}>
                <TextField
                  autoComplete="first_name"
                  name="first_name"
                  variant="outlined"
                  required
                  fullWidth
                  id="first_name"
                  label="First Name"
                  value={first_name}
                  onChange={this.onChange}
                  autoFocus
                />
              </Grid>
              <Grid item xs={6} sm={6}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="last_name"
                  label="Last Name"
                  name="last_name"
                  autoComplete="last_name"
                  value={last_name}
                  onChange={this.onChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="phone_number"
                  label="Mobile Number"
                  name="phone_number"
                  value={phone_number}
                  onChange={this.onChange}
                  autoComplete="phone_number"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  value={email}
                  onChange={this.onChange}
                  autoComplete="email"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  value={password}
                  onChange={this.onChange}
                  autoComplete="current-password"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="password2"
                  label="Confirm Password"
                  type="password"
                  id="confirm-password"
                  value={password2}
                  onChange={this.onChange}
                  autoComplete="confirm-password"
                />
              </Grid>
              <ListItem className={classes.title}>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={checkedC}
                      name="checkedC"
                      onChange={this.handleChange}
                      value="checked"
                    />
                  }
                  label="I accept the terms and conditions."
                  required
                />
              </ListItem>
            </Grid>
            <Grid item>
              <Link href="/login" variant="body2">
                Terms and Conditions
              </Link>
            </Grid>
            <Button
              className={classes.submit}
              type="submit"
              disabled={!isValid}
              fullWidth
              variant="contained"
              color="primary"
            >
              Sign Up
            </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="/login" variant="body2">
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
        <Box mt={5}></Box>
      </Container>
    );
  }
}

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  isBuyer: state.auth.user,
});

// export default withStyles(styles, { withTheme: true })(Register);
export default connect(mapStateToProps, { register, createMessage })(
  withStyles(styles)(Register)
);
