import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import AssignmentIcon from "@material-ui/icons/Assignment";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getTransactions } from "../../actions/transactions";

const GeneratedCode = ({ getTransactions, transactions }) => {
  const [copySuccess, setCopySuccess] = useState("");
  useEffect(() => {
    getTransactions();
  }, [getTransactions]);
  // getTransactions();
  // const gencode = "00-EFACDB06-BF1C-4183-ABB3-555FEF927358";
  const gencode = transactions.map((transaction) => transaction.form_pk);
  const transaction_link = `http://localhost:3000/api/transactions/?form_pk=${gencode}`;

  // your function to copy here

  const copyToClipBoard = async (copyMe) => {
    try {
      await navigator.clipboard.writeText(copyMe);
      setCopySuccess("Copied!");
    } catch (err) {
      setCopySuccess("Failed to copy!");
    }
  };

  return (
    <div>
      <Box style={{ margin: "50px 0 50px" }}>
        <Typography variant="h4" align="center" style={{ padding: "20px" }}>
          Transaction Code
        </Typography>
        <Typography variant="body1" align="center">
          Share this link with the Seller
        </Typography>
        <Typography
          variant="subtitle2"
          align="center"
          style={{ margin: "20px" }}
        >
          <b>Transaction Link:</b> {transaction_link}
        </Typography>
      </Box>
      <div>
        <Button
          style={{ padding: "10px" }}
          variant="contained"
          color="secondary"
          fullWidth
          startIcon={<AssignmentIcon />}
          onClick={() => copyToClipBoard(transaction_link)}
        >
          Click here to copy
        </Button>
      </div>
      <div style={{ textAlign: "center" }}>{copySuccess}</div>
    </div>
  );
};

GeneratedCode.propTypes = {
  transactions: PropTypes.array.isRequired,
  getTransactions: PropTypes.func.isRequired,
  // deleteTransaction: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  transactions: state.transactions.transactions,
});
// export default GeneratedCode;
export default connect(mapStateToProps, { getTransactions })(GeneratedCode);
