import React from "react";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

import InputAdornment from "@material-ui/core/InputAdornment";

// Destructuring props
const SecondStep = ({
  handleNext,
  handleBack,
  handleChange,
  values: { amount, cpty_phone, service_details, cpty_email },
  formErrors,
}) => {
  // Check if all values are not empty or if there are some error
  const isValid =
    service_details.length > 0 &&
    !formErrors.serviceDetails &&
    cpty_email.length > 0 &&
    !formErrors.cpty_email &&
    amount.length > 0 &&
    !formErrors.amount &&
    cpty_phone.length > 0 &&
    !formErrors.cpty_phone;

  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="Transaction Amount (Maximum amount - Rs. 100000)"
            name="amount"
            placeholder="Enter Amount"
            margin="normal"
            value={amount || ""}
            onChange={handleChange}
            error={!!formErrors.amount}
            helperText={formErrors.amount}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">₹</InputAdornment>
              ),
            }}
            required
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="Registered Seller's Mobile Number"
            name="cpty_phone"
            placeholder="i.e: xxxxx-xxxxx"
            value={cpty_phone || ""}
            onChange={handleChange}
            error={!!formErrors.cpty_phone}
            helperText={formErrors.cpty_phone}
            margin="normal"
            required
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            label="Registered Seller's Email ID"
            name="cpty_email"
            placeholder="Seller's email address"
            type="email"
            value={cpty_email || ""}
            onChange={handleChange}
            margin="normal"
            error={!!formErrors.cpty_email}
            helperText={formErrors.cpty_email}
            required
          />
        </Grid>
      </Grid>
      <div
        style={{ display: "flex", marginTop: 50, justifyContent: "flex-end" }}
      >
        <Button
          variant="contained"
          color="default"
          onClick={handleBack}
          style={{ marginRight: 10 }}
        >
          Back
        </Button>
        <Button
          variant="contained"
          disabled={!isValid}
          color="primary"
          onClick={isValid ? handleNext : null}
        >
          Next
        </Button>
      </div>
    </>
  );
};

export default SecondStep;
