import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { reset_password_confirm } from "../../actions/auth";

import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { createMessage } from "../../actions/messages";
import formValidation from "../../Helper/formValidation";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const fieldsValidation = {
  new_password: {
    error: "",
    validate: "password",
    minLength: 8,
    maxLength: 20,
  },
  re_new_password: {
    error: "",
    validate: "",
  },
};

const ResetPasswordConfirm = (props) => {
  const classes = useStyles();
  const [requestSent, setRequestSent] = useState(false);
  const [formErrors, setFormErrors] = useState({});
  // formErrors.re_new_password = "Passwords do not match";
  const [formData, setFormData] = useState({
    new_password: "",
    re_new_password: "",
  });

  const { new_password, re_new_password } = formData;
  const isValid =
    new_password.length > 0 &&
    !formErrors.new_password &&
    re_new_password.length > 0 &&
    // !formErrors.re_new_password &&
    new_password === re_new_password;

  const onChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [e.target.name]: e.target.value });
    const error = formValidation(name, value, fieldsValidation) || "";

    setFormErrors({
      [name]: error,
    });
  };
  const onSubmit = (e) => {
    e.preventDefault();

    const uid = props.match.params.uid;
    const token = props.match.params.token;
    console.log(uid, token, new_password, re_new_password);
    if (new_password !== re_new_password) {
      // createMessage("Passwords do not match");
      formErrors.re_new_password = "Passwords do not match";
      alert("Not equal");
    } else {
      props.reset_password_confirm(uid, token, new_password, re_new_password);
      setRequestSent(true);
    }
  };

  if (requestSent) {
    props.createMessage("Passwords matched!");
    alert("Password has been reset.");
    return <Redirect to="/" />;
  }
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Enter New Password
        </Typography>
        <form className={classes.form} onSubmit={(e) => onSubmit(e)} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="new_password"
                label="New Password"
                type="password"
                id="password"
                value={new_password}
                onChange={(e) => onChange(e)}
                autoComplete="current-password"
                error={!!formErrors.new_password}
                helperText={formErrors.new_password}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="re_new_password"
                label="Confirm New Password"
                type="password"
                id="confirm-password"
                value={re_new_password}
                onChange={(e) => onChange(e)}
                autoComplete="confirm-password"
                error={!!formErrors.re_new_password}
                helperText={formErrors.re_new_password}
              />
            </Grid>
          </Grid>
          <Button
            className={classes.submit}
            disabled={!isValid}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
          >
            Reset Password
          </Button>
        </form>
      </div>
      <Box mt={5}></Box>
    </Container>
  );
};

// const mapStateToProps = (state) => ({
//   isAuthenticated: state.auth.isAuthenticated,
// });

export default connect(null, {
  reset_password_confirm,
  createMessage,
})(ResetPasswordConfirm);
