import * as React from "react";
import {
  AppBar,
  Toolbar,
  Container,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Hidden,
  Typography,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core";
import { Home } from "@material-ui/icons";
import SideDrawer from "./SideDrawer";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import Button from "@material-ui/core/Button";

import { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { logout } from "../../actions/auth";
import CardMedia from "@material-ui/core/CardMedia";
import secufer_logo from "./images/secufer_logo_name.png";

const styles = (theme) => ({
  navbarDisplayFlex: {
    display: `flex`,
    justifyContent: `space-between`,
  },
  navDisplayFlex: {
    display: `flex`,
    justifyContent: `space-between`,
  },
  linkText: {
    textDecoration: `none`,
    textTransform: `uppercase`,
    color: `white`,
    paddingTop: 20,
  },
  linkLogo: {
    paddingLeft: 10,
    paddingTop: 2,
  },
  button: {
    // margin: theme.spacing(1),
    // paddingTop: 10,
    // fontSize: 16,
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
    // marginLeft: 100,
    marginRight: 10,
    paddingTop: 8,
    fontSize: 16,
  },
});

const guestLinks = [
  { title: `home`, path: `/` },
  { title: `about us`, path: `/about us` },
  { title: `faq`, path: `/faq` },
  { title: `disputes`, path: `/disputes` },
];

export class NavbarNew extends Component {
  static propTypes = {
    auth: PropTypes.object.isRequired,
    logout: PropTypes.func.isRequired,
  };
  render() {
    const { classes } = this.props;
    const { isAuthenticated, user } = this.props.auth;

    const preventDefault = (event) => event.preventDefault();

    return (
      <AppBar style={{ backgroundColor: "#7B4B94" }} position="static">
        {/* #D0CCD0 */}
        <Toolbar>
          <Container maxWidth="lg" className={classes.navbarDisplayFlex}>
            <img
              src={secufer_logo}
              alt="Not loading"
              style={{
                paddingLeft: 10,
                paddingTop: 2,
                height: "100px",
                width: "180px",
              }}
            ></img>
            {/* <IconButton
              component={Link}
              to={"/"}
              edge="start"
              color="inherit"
              aria-label="home"
            > */}
            {/* <img src=""></img> */}
            <Typography variant="h5" className={classes.linkLogo}>
              {/* Secufer */}
            </Typography>
            {/* </IconButton> */}

            <Hidden smDown>
              <List
                component="nav"
                aria-labelledby="main navigation"
                className={classes.navDisplayFlex}
              >
                {isAuthenticated ? (
                  <Button
                    // href="/"
                    component={Link}
                    to={"/"}
                    variant="outlined"
                    color="inherit"
                    size="large"
                    className={classes.button}
                    startIcon={<AccountCircleIcon />}
                    onClick={this.props.logout}
                  >
                    Sign out {user.first_name}
                  </Button>
                ) : (
                  <Button
                    href="/login"
                    variant="outlined"
                    color="inherit"
                    size="large"
                    className={classes.button}
                    startIcon={<AccountCircleIcon />}
                  >
                    Sign-in
                  </Button>
                )}
                {/* <Link to="/">Home</Link>
                <Link to="/seller_form">Seller</Link> */}

                {guestLinks.map(({ title, path }) => (
                  // <a href={path} key={title} className={classes.linkText}>
                  <Link to={path} className={classes.linkText}>
                    <ListItem button>
                      <ListItemText primary={title} />
                    </ListItem>
                  </Link>
                  // {/* </a> */}
                ))}
              </List>
            </Hidden>
            <Hidden mdUp>
              <List
                component="nav"
                aria-labelledby="main navigation"
                className={classes.navDisplayFlex}
              >
                {isAuthenticated ? (
                  <Button
                    // href="/"
                    component={Link}
                    to={"/"}
                    variant="outlined"
                    color="inherit"
                    size="large"
                    className={classes.button}
                    startIcon={<AccountCircleIcon />}
                    onClick={this.props.logout}
                  >
                    Sign out {user.fname}
                  </Button>
                ) : (
                  <Button
                    href="/login"
                    variant="outlined"
                    color="inherit"
                    size="large"
                    className={classes.button}
                    startIcon={<AccountCircleIcon />}
                  >
                    Sign-in
                  </Button>
                )}
                <SideDrawer navLinks={guestLinks} />
              </List>
            </Hidden>
          </Container>
        </Toolbar>
      </AppBar>
    );
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { logout })(
  withStyles(styles)(NavbarNew)
);
